.header {
    display: flex;
    background-color: #083464;
    padding: .5em 1em;
    width: 100%;
    align-items: center;
    box-sizing: border-box;
}

.title {
    margin: 0;
    margin-left: 26px;
    font-family: 'Open Sans', sans-serif;
    color: #ffffff;
}

.loginBtn {
    background: none;
    border: none;
    outline: none;
    color: #F28D00;
    font-family: 'Open Sans', sans-serif;
    font-size: 1em;
    padding: 0;
    margin-left: 2em;
    cursor: pointer;
}
.loginBtn:hover {
    text-decoration: underline;
}

.menu { 
    border: 2px solid #00000090;
}
.toggle {
    width: 140px!important;
    font-weight: bold;
    justify-content: space-between;
}

.toggle,
.slideBox button {
    display: flex;
    align-items: center;
    background: #ffffff;
    outline: none;
    font-family: 'Open Sans', sans-serif;
    padding: .3em 1em;
    font-size: 11pt;
    border: none;
    border-bottom: 1px solid #00000090;
    cursor: pointer;
}

.slideBox button {
    gap: 1em;
}

.toggle:hover,
.slideBox button:hover {
    background-color: #ebebeb;
}

.slideBox  {
    padding-top:.1em;
    box-sizing: border-box;
    width: 140px!important;
} 