.periodHeader {
    display: flex;
    height: 32px;
}
.periodHeader > * {
    font-family: 'Open Sans', sans-serif;
    margin: 0;
    font-size: .9em;
}

.lukuvuosiHeader {
    display: flex;
    height: 32px;
    margin-top: 10px;
}
.lukuvuosiHeader > *  {
    font-family: 'Open Sans', sans-serif;
    margin: 0;
    font-size: .9em;
}

.periodHolder {
    display: flex;
    flex-direction: column;
    border: 1px solid #00000040;
    width: 260px;
    row-gap: 2px;
}
.summerPeriod {
    
    border-right: 1px solid #002f0060;
    border-left: 1px solid #002f0060;
    box-shadow: 0 0 1px #002f0060;
}

.periodCard {
    display: flex;
    flex-direction: column;
    padding: 1px 2px;
}

.periodCardHeader {
    background-color: #ebebeb;
    padding: .3em .2em;
    border-bottom: 1px solid #00000040;
}
.periodCardHeader :is(h3) {
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    font-size: .9em;
}
.periodCardInfo {
    display: flex;
    align-items: center;
    /* background-color: whitesmoke; */
    padding: 4px 0;
}

.periodCardInfo :is(p){
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    font-size: 10pt;
}
.periodCardInfo :is(p):last-of-type {
    margin-left: auto;
}

.menuItem  {
    display: flex;
    align-items: center;
    cursor: move;
    width: 100%;
}
.menuItem:hover {
    background-color: #ebebeb;
}

.rowItem {
    display: grid;
    place-items: center;
    max-width: 68%;
}
.rowItem > p {
    max-width: 156px;
}
.requiredColor {
    margin-right: 6px;
    height: 12px;
    width: 12px;
    border: 1px solid #0000003e;
    border-radius: 50%;
}
.pakollinen {
    background-color: orange;
}
.valinnainen {
    background-color: lightblue;
}
.op {
    display: block;
    margin-left: auto;
    margin-right: 6px;
    font-weight: bold;
    font-family: 'Open Sans', sans-serif;
    font-size: 10pt;
}
.op * {
    font-weight: bold;
}

.sqr {
    margin-right: 6px;
    height: 11px;
    width: 11px;
    border: 1px solid #0000003e;
    box-sizing: border-box;
}
.amm {
    background-color: #008000;
}
.yto {
    background-color: #c1c70f;
}

.removeItemBtn {
    border: none;
    display: grid;
    place-items: center;
    background: none;
    cursor: pointer;
}
.removeItemBtn :is(svg) {
    color: #8b0000;
    font-size: 15px;
}
.removeItemBtn:hover :is(svg) {
    color: #620202;
}
