

.container {
    display: grid;
    place-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: .3em;
    z-index: 100;
    box-sizing: border-box;
    overflow: hidden;
    background-color: #ebebebdf;

    font-family: 'Open Sans', sans-serif;
}

.container :is(h1,h2,h3) {
    color: #ffffff;
    font-size: 36px;
    text-align: center;
    width: 100%;
}

.box {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    max-height: 90vh;
    overflow-y: auto;
    width: 100%;
    max-width: 280px;
    animation: appearing .6s ease-in-out forwards alternate;
}
.box::-webkit-scrollbar {
    display: none;
}

@keyframes appearing {
    from {
        opacity: 0;
        transform: translateY(-200%);
    }
    to {
        opacity: 1;
        transform: translateY(0%);
    }
}

.topBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: .5em;
    padding: 1em 0;
}
.topBar > * {
    margin: 0;
}

.box > form {
    display: flex;
    flex-direction: column;
    width: 100%;
    box-sizing: border-box;
    gap: .5em;
    background-color: #083464;
    border-radius: 10px;
    box-shadow: 0 .5em 1em #00000070;
    padding: 1em;
}
.formReg {
    gap: .7em!important;
}

.formRow {
    display: flex;
    align-items: center;
    gap: 5px;
    box-sizing: border-box;
    width: 100%;
}
.formRow > input {
    outline: none;
    font-family: 'Open Sans', sans-serif;
    box-sizing: border-box;
    padding: .5em 14px;
    border-radius: 2px;
    border: none;
    width: 100%;
    background-color: #ffffff;
}
.formRow > input:focus {
    background-color: #ebebeb;
}
.formRow > button {
    outline: none;
    font-family: 'Open Sans', sans-serif;
}
.formRow > input[type=checkbox] {
    width: fit-content;
    margin: 3px 0 0 0;
    padding: 0;
}
.conds {
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    margin: 1em 0;
}

.last {
    margin-top: .5em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 0;
    box-sizing: border-box;
    padding: 0 14px;
}

.submit {
    font-family: 'Open Sans', sans-serif;
    width: 100%;
    height: 82px;
    font-size: 1.2em;
    border: none;
    color: #ffffff;
    border-radius: .5em;
    margin-top: .5em;
    background-color: #4D8400;
    cursor: pointer;
}
.submit:hover {
    background-color: #3d6604;
}
.last > button {
    outline: none;
    border: none;
    margin: none;
    padding: none;
    background: none;
    cursor: pointer;
    font-family: 'Open Sans', sans-serif;
}
.last > button:hover {
    text-shadow: 4px 2px 4px #00000090;
}


.forgottenPwdBtn {
    color: #0974B3;
}
.createNewUserBtn {
    color: #4D8400;
}
.cancelBtn {
    color: #ffffff;
    font-size: 1.3em;
    background: #8b0000!important;
    padding: .6em .6em;
    border-radius: .5em;
    width: 100%;
    margin-bottom: 1em;
}
.cancelBtn:hover {
    background: #6c0101!important;
}

.apply {
    align-self: flex-start;
}

.applyCheck {
    color: #ffffff!important;
    font-family: 'Open Sans', sans-serif;
    /* align-self: ; */
    font-size: 11pt;
}

.termsAndCondsBtn {
    padding: 0;
    border: none;
    outline: none;
    color: #61afdc;
    font-style: italic;
    margin-left: .3em;
    cursor: pointer;
    background: none;
    font-family: 'Open Sans', sans-serif;
    font-size: 11pt;
}
.termsAndCondsBtn:hover {
    color: #0974B3;
    text-decoration: underline;
}